export default {
  components: {},
  data: function data() {
    return {};
  },
  methods: {
    adminConnected: function adminConnected() {
      if (localStorage.getItem("admin")) {
        return true;
      }
    },
    adminDisconnected: function adminDisconnected() {
      localStorage.removeItem("admin");
      this.$router.push("/admin");
    }
  }
};