import Modale from "./Modale.vue";

var axios = require("axios");

export default {
  components: {
    Modale: Modale
  },
  data: function data() {
    return {
      picture: [],
      pictures: [],
      name: null,
      price: null,
      description: null,
      revele: false,
      imageCanvas: null
    };
  },
  mounted: function mounted() {
    var _this = this;

    axios.get("/your").then(function (res) {
      _this.pictures = res.data;
      console.log(_this.pictures);
    }).catch(function (error) {
      // handle error
      console.log(error);
    }).then(function () {// always executed
    });
  },
  methods: {
    nameP: function nameP(picture) {
      console.log(picture.name);
    },
    toggleModale: function toggleModale(picture) {
      this.revele = !this.revele;
      this.imageCanvas = picture.picture;
      this.name = picture.name;
      this.price = picture.price;
      this.description = picture.description;
      console.log(picture.picture);
    }
  }
};